<template lang="html">
  <div class="">
    <default-head header="搜索地点"></default-head>
    <div class="search-input-map">
      <search-input @ok-handle="getPosition(address)" btnText="确定" @keyup-search="keyUpSearch" placeholder="搜索您现在的位置"></search-input>
    </div>
    <div class="baidu-map-box">
      <div id="baidu-map"></div>
    </div>
    <ul class="result-box" v-if="addArr.length > 0">
      <li class="result-item" v-for="item in addArr" @click="getPosition(item)">
        <div class="result-item__title">{{item.title}}</div>
        <div class="result-item__address">{{item.address}}</div>
      </li>
    </ul>
  </div>
</template>

<script>
import utils from '@/utils/utils'
import minutils from '@/utils/minUtils'
import defaultHead from '@/components/head/defaultHead'
import searchInput from '@/components/common/searchInput'
export default {
  components: {defaultHead, searchInput},
  data () {
    return {
      map: {},
      addArr: [],
      address: ''
    }
  },
  mounted () {
    this.initPage()
  },
  methods: {
    // 页面初始化加载当前位置地图
    initPage () {
      let self = this
      minutils.IndicatorOpen('加载中...')
      utils.initCurrentMap({
        idBox: 'baidu-map',
        callback: function (map, data) {
          self.map = map
          self.address = data
          self.$nextTick(() => {
            minutils.IndicatorClose()
          })
        },
        dragendHandle: this.dragendHandle
      })
    },
    keyUpSearch (val) {
      utils.searchComplete(this.map, val, this.addArr = [])
    },
    dragendHandle (address) {
      var postion = address.city + address.district + address.street + address.streetNumber
      this.address = address
      this.keyUpSearch(postion)
    },
    getPosition (address) {
      sessionStorage.setItem('searchPosition', JSON.stringify(address))
      this.$router.push({path: 'cardaddaddress', query: {sign: this.$route.query.sign}})
    }
  }
}
</script>

<style lang="less">
@import '../assets/css/variable.less';
.search-input-map {
  position: fixed;
  top: .88rem;
  width: 100%;
  background: @bgColor;
  z-index: 10;
}
.baidu-map-box {
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
  left: 0;
}
#baidu-map {
  height:100%;
  width: 100%;
}
.result-box {
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  background: @whiteColor;
  font-size: .3rem;
  height: 5.2rem;
  overflow-y: auto;
  .result-item {
    padding: .2rem .22rem;
    border-bottom: 1px solid @lineColor;
    .result-item__title {
      color: @defaultColor;
    }
    .result-item__address {
      margin-top: .2rem;
      color: @lightColor;
    }
  }
  .result-item:last-child {
    border: none
  }
}
</style>



